
import Api from "@/js/common";
import Vue from "vue";
import Selector from 'vux/src/components/selector/index.vue'

import { mapGetters, mapMutations } from "vuex";
import { setTimeout } from "timers";

export default {
data() {
return {
active: false,
answer: [],
selectAnswer: {}
};
},
props: ["quesId", "quesTitle", "quesMustbe", "quesAnswer", "quesIndex"],
methods: {
...mapMutations("submit", ["newAnswer"]),
selectStyle(val) {
if (val) {
var type = this.quesId;
this.selectAnswer[type] = val;
this.newAnswer(this.selectAnswer);
}
}
},
created() {
setTimeout(() => {
var answer = [];
var quesAnswer = "";
if (typeof this.quesAnswer == "string") {
quesAnswer = JSON.parse(this.quesAnswer);
} else {
quesAnswer = this.quesAnswer;
}
for (var i in quesAnswer) {
quesAnswer[i].index = i;
answer.push(quesAnswer[i]);
}
this.answer = answer;
}, 1000);
},
components: {
Selector
}
};



