
import Api from '@/js/common'
import Vue from 'vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { mapGetters, mapMutations } from 'vuex'

export default {
data() {
return {
active: false,
selectAnswer:{},
}
},
props: ['quesId', 'quesTitle', 'quesMustbe','quesIndex'],
methods: {
...mapMutations('submit', ['newAnswer']),
selectStyle(val) {
var type = this.quesId
this.selectAnswer[type] = val
this.newAnswer(this.selectAnswer)
}
},
components:{
XInput
}
}



