
import Api from '@/js/common'
import Vue from 'vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { mapGetters, mapMutations } from 'vuex'

export default {
data() {
return {
active: false,
selectAnswer:{}
}
},
props: ['quesId', 'quesTitle', 'quesMustbe','quesIndex'],
methods: {
...mapMutations('submit', ['newAnswer']),
selectStyle(val) {
var type = this.quesId
this.selectAnswer[type] = this.creatFormatCode(val)
this.newAnswer(this.selectAnswer)
},
creatFormatCode(strValue){
return strValue.replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, '&nbsp;');
}
},
components:{
XTextarea
}
}



