
import Popover from 'vux/src/components/popover/index.vue'

import Api from "@/js/common";

export default {
data() {
return {
showHide:true
};
},
props: [
"headTitle",
"goBack",
"headRight",
"headSearch",
"headSwitch",
"headHide"
],
methods: {
goToGreat() {
Api.setVal("questionList", "[]");
Api.setVal("questionTitle", "");
Api.setVal("questionDes", "");
Api.setVal("questionEndTime", "");
Api.setVal("questionEndWord", "");
this.$router.push("/creatQuestion");
},
onShow() {
console.log("on show");
},
onHide() {
console.log("on hide");
},
goSearch() {
this.$router.push("/search");
},
preview() {
this.$emit("previewClick", { a: 1, b: 2 });
},
quesDelete() {
this.$emit("popAction", "delete");
},
investigate() {
this.$emit("popAction", "investigate");
},
quesEdit() {
this.$emit("popAction", "edit");
},
quesPublish() {
this.$emit("popAction", "publish");
},
quesShare() {
this.$emit("popAction", "share");
},
backBtn() {
this.$router.go(-1);
},
goBack1() {
if (this.headSwitch == "index") {
try {
uexWidget.finishWidget("", "ZGZTWJDC", "0");
} catch (error) {}
} else {
this.$router.go(-1);
}
}
},
components: {
Popover
},
mounted(){
this.showHide = appcan.IS_APP
}
};



