import { mapGetters, mapMutations } from "vuex";
import Api from "@/js/common";
import Vue from "vue";
import { setTimeout } from "timers";

export default {
  data() {
    return {
      active: false,
      answer: [],
      selectAnswer: {}
    };
  },
  props: ["quesId", "quesTitle", "quesMustbe", "quesAnswer", "quesIndex"],
  methods: {
    ...mapMutations("submit", ["newAnswer"]),
    selectStyle(item, index) {
      var selectAnswer = "";
      var _this = this;
      this.$nextTick(function() {
        var select = !item.select;
        Vue.set(item, "select", select);
        _this.answer.forEach(function(item) {
          if (item.select) {
            if (selectAnswer == "") {
              selectAnswer = item.index;
            } else {
              selectAnswer = selectAnswer + "," + item.index;
            }
          }
        });
        var type = _this.quesId;
        _this.selectAnswer[type] = selectAnswer;
        _this.newAnswer(_this.selectAnswer);
      });
    }
  },
  created() {
    setTimeout(() => {
      var answer = [];
      var quesAnswer = "";
      if (typeof this.quesAnswer == "string") {
        quesAnswer = JSON.parse(this.quesAnswer);
      } else {
        quesAnswer = this.quesAnswer;
      }
      for (var i in quesAnswer) {
        quesAnswer[i].index = i;
        answer.push(quesAnswer[i]);
      }
      this.answer = answer;
    }, 1000);
  }
};



